  overflow: hidden;
  position: relative; }
  .hp-featuredimage__imgwrap img {
    width: 100%; }
  @media (min-width: 768px) {
    .hp-featuredimage {
      background-color: #000;
      max-height: 55vh;
      display: flex;
      justify-content: center;
      align-items: center; }
      .hp-featuredimage__imgwrap {
        min-height: 300px;
        flex: 1 100%; }
      .hp-featuredimage__contentwrap {
        max-width: 1260px;
        color: #fff;
        position: absolute;
        z-index: 1;
        text-align: center; }
      .hp-featuredimage h1 {
        text-shadow: 0 1px 1px #000; }
      .hp-featuredimage::after {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        content: "";
        transition: all .5s ease-in-out;
        background: rgba(0, 0, 0, 0.5);
        will-change: opacity; } }

.featured-cards .card:after {
  content: '';
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }

.featured-cards .card-img-overlay {
  z-index: 1; }

